import DateFnsUtils from '@date-io/date-fns';
import { fi } from 'date-fns/locale';
import { Button, Container, InputLabel, makeStyles, TextField, Typography } from '@material-ui/core'
import { DateTimePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import React, { useEffect, useState } from 'react'
import { v4 as uuid } from 'uuid';
import { useHistory, useParams } from 'react-router';

const useStyles = makeStyles({
    taulukko : {
      width: 750,
      border: "solid black 2px",
      boxShadow: "0px 0px 3px black",
      padding: 20,
      backgroundColor:"lightgray"
    },
    teksti : {
        backgroundColor: "white",
        marginTop: 5,
        marginBottom : 10
    }
  })

function MuokkaaSaatava(props) {

    const { id } = useParams();
    const history = useHistory();

    const [tiedot, setTiedot] = useState(false);
    const [nimiError, setNimiError] = useState(false);
    const [nimiErrorViesti, setNimiErrorViesti] = useState("");
    const [summaError, setSummaError] = useState(false);
    const [summaErrorViesti, setSummaErrorViesti] = useState("");

    const tyylit = useStyles()

    const [pvm, setPvm] = useState(new Date(props.saatavat.filter((saatava) => {
        return (saatava.id === id)})[0].lisayspaiva))
    const [nimi, setNimi] = useState(props.saatavat.filter((saatava) => {
        return (saatava.id === id)})[0].henkilonnimi);
    const [summa, setSumma] = useState(props.saatavat.filter((saatava) => {
        return (saatava.id === id)})[0].rahasumma);

        const muokkaaSaatava = () => {
            let uusiHenkilo = {
            id : uuid(),
            lisayspaiva : pvm.getTime(),
            henkilonnimi : nimi,
            rahasumma : Number(summa)
}
        if (tiedot) {
            for (let i = 0; i < props.saatavat.length; i++){
                if (id === props.saatavat[i].id) {
            props.saatavat.splice(props.saatavat.indexOf(props.saatavat[i]), 1, uusiHenkilo)
            props.saatavat.sort(function(a, b) {
            return a.lisayspaiva - b.lisayspaiva
            })
        props.saatavat.reverse()
        props.setSaatavat([...props.saatavat])
        history.push("/saatavat")
            }
        }
    }
    else tietojenTarkistus();
    }

    const tietojenTarkistus = () => {
        if (!nimi) {
            setNimiError(true); 
            setNimiErrorViesti("Pakollinen tieto")
        }
        if (!summa) {
            setSummaError(true); 
            setSummaErrorViesti("Pakollinen tieto")
        }
        if (isNaN(summa) === true) {
          setSummaError(true); 
          setSummaErrorViesti("Summan täytyy olla numero")
      }
    }

    useEffect(() => {
        if ((nimi) && (summa) && (isNaN(summa) === false)) {
            setTiedot(true)
        }
        else setTiedot(false);
      }, [nimi, summa])

    return (
        <Container className={tyylit.taulukko}>
            <MuiPickersUtilsProvider
            className={tyylit.tekstiasettelu}
            utils={DateFnsUtils} 
            locale={fi}
            >

            <Typography 
            variant="h2" 
            style={{textAlign:"center", fontWeight:"bold"}}
            >Muokkaa</Typography>

            <InputLabel>Muokkaa nimeä</InputLabel>
            <TextField
            className={tyylit.teksti}
            value={nimi}
            variant="outlined"
            fullWidth
            placeholder="Nimi"
            helperText={nimiErrorViesti}
            error={nimiError}
            onChange={(e) => {setNimi(e.target.value)}}
            />

            <InputLabel>Muokkaa summaa</InputLabel>
            <TextField
            className={tyylit.teksti}
            value={summa}
            variant="outlined"
            fullWidth
            placeholder="Nimi"
            helperText={summaErrorViesti}
            error={summaError}
            onChange={(e) => {setSumma(e.target.value)}}
            />

            <InputLabel>Muokkaa lisäyspäivää</InputLabel>
            <DateTimePicker
            disableFuture
            ampm={false}
            className={tyylit.teksti}
            fullWidth
            inputVariant="outlined"
            format="d.M.y HH:mm"
            value={pvm}
            onChange={setPvm}
            />

            <Button
            style={{marginTop:"10px", marginLeft:"10px"}}
            variant="contained"
            color="default"
            size="large"
            onClick={muokkaaSaatava}
            >
                Muokkaa
            </Button>

            <Button
            style={{marginTop:"10px", marginLeft:"10px"}}
            variant="contained"
            color="default"
            size="large"
            onClick={() => {
                history.push("/saatavat")
            }}
            >
                Peruuta
            </Button>
        </MuiPickersUtilsProvider>
        </Container>
    )
}

export default MuokkaaSaatava
