import React, { useEffect } from 'react'
import { useState } from 'react'
import { Button, FormControl, IconButton, InputLabel, MenuItem, Select, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core';
import { Build, Delete } from '@material-ui/icons';
import { format, getTime } from 'date-fns';
import { Link } from 'react-router-dom';
import { v4 as uuid } from 'uuid';
import ClearIcon from '@material-ui/icons/Clear';


const useStyles = makeStyles({
    taulukko : {
      width: 750,
      border: "solid black 2px",
      boxShadow: "0px 0px 3px black",
      padding: 20,
      backgroundColor:"lightyellow"
    }
  })

function Saatavat(props) {

    const tyylit = useStyles();

    //Error viestit
    const [tiedot, setTiedot] = useState(false);
    const [nimiError, setNimiError] = useState(false);
    const [nimiErrorViesti, setNimiErrorViesti] = useState("");
    const [summaError, setSummaError] = useState(false);
    const [summaErrorViesti, setSummaErrorViesti] = useState("");

    //Velkalistan muuttujat
    const [summa, setSumma] = useState();
    const [nimi, setNimi] = useState();
    const [pvm, setPvm] = useState(new Date().getTime())
    const [kello, setKello] = useState(new Date())
    const [arraytest, setArraytest] = useState()
    // const [summienSumma, setsummienSumma] = useState();
    // const [props.summaBeta, props.setSummaBeta] = useState([]);
  
    // //Arrayt
    // const [props.saatavat, props.setSaatavat] = useState([]);
  
  //Velkalistan funktiot
  let uusiHenkilo = {
    id : uuid(),
    lisayspaiva : pvm,
    henkilonnimi : nimi,
    rahasumma : Number(summa)
  }

  // let sum = 0 + Number(summa); //Alustus for-looppia varten

    const lisaaSaatava = () => {
    if (tiedot) {
      if (props.saatavat.length > 0) {
        for (let i = 0; i < props.saatavat.length; i++){
            if (getTime(pvm) > getTime(props.saatavat[i].lisayspaiva)) {
                props.saatavat.splice(i, 0, uusiHenkilo);
                props.setSaatavat([...props.saatavat])
                break;
            }
            else props.setSaatavat([...props.saatavat, uusiHenkilo]);
            }
        }
        else props.setSaatavat([...props.saatavat, uusiHenkilo]);
    } else tietojenTarkistus();
        // setsummienSumma(summa)
    // for(let i = 0;i < props.saatavat.length;i++){
    //     sum += props.saatavat[i].rahasumma;
    //     setsummienSumma(sum)
    //     }
  }

  const tietojenTarkistus = () => {
    if (!nimi) {
        setNimiError(true); 
        setNimiErrorViesti("Pakollinen tieto")
    }
    if (!summa) {
        setSummaError(true); 
        setSummaErrorViesti("Pakollinen tieto")
    }
    if (isNaN(summa) === true) {
      setSummaError(true); 
      setSummaErrorViesti("Summan täytyy olla numero")
  }
}

    useEffect(() => {
      if (props.saatavat.length){
        let sum = props.saatavat.reduce((total, raha) =>{
          return total += raha.rahasumma
        }, 0)
        setArraytest(Number(sum))
        props.setSaatavasumma(Number(sum))
      }
      else {setArraytest(0); props.setSaatavasumma(0)}
    }, [props.saatavat])


    const poista = (indeksi) => {
    props.saatavat.splice(indeksi, 1)
    // for(let i = 0;i < props.saatavat.length;i++){
    //     sum += props.saatavat[i].rahasumma;
    //     setsummienSumma(sum)
    //     }
    // setsummienSumma(sum - Number(summa));
    props.setSaatavat([...props.saatavat])
    }

    useEffect(() => {
      if ((nimi) && (summa) && (isNaN(summa) === false)) {
          setTiedot(true)
      }
      else setTiedot(false);
    }, [nimi, summa])

    return (
        <div style={{width:"750px", margin:"auto"}}>
    <TableContainer className={tyylit.taulukko}>
      <Typography variant="h2" style={{textAlign:"center", fontWeight:"bold", color:"white",textShadow:"2px 2px 3px black"}}>Saatavat</Typography>
      <Table>
        <TableHead>
          <TableRow>
          <TableCell><Typography variant="h5" style={{fontWeight:"bold"}}>Lisäyspäivä</Typography></TableCell>
            <TableCell><Typography variant="h5" style={{fontWeight:"bold"}}>Kenelle</Typography></TableCell>
            <TableCell><Typography variant="h5" style={{fontWeight:"bold"}}>Summa</Typography></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
        {
        props.saatavat.map( (saatava, idx) => {
              return (
        <TableRow key={idx}>
          
          <TableCell>{format(saatava.lisayspaiva, "d.M.y HH:mm")}</TableCell>
          <TableCell>{saatava.henkilonnimi}</TableCell>
          <TableCell>{saatava.rahasumma} €</TableCell>
          
          <IconButton onClick={ () => {
            poista(idx);
          }}><Delete/></IconButton>

          <IconButton
          component={Link}
          to={`/muokkaasaatava/${saatava.id}`}
          ><Build/></IconButton>
        </TableRow>
        )
        })}
        <TableRow>

          <TableCell>
          &nbsp;
          </TableCell>

          <TableCell>

            <TextField 
            placeholder="Kenelle"
            variant="outlined"
            helperText={nimiErrorViesti}
            error={nimiError}
            onChange={ (e) => { setNimi(e.target.value) } }
            />
            </TableCell>

          <TableCell>

            <TextField 
            placeholder="Summa (€)"
            variant="outlined"
            helperText={summaErrorViesti}
            error={summaError}
            onChange={ (e) => { setSumma(e.target.value ) } } 
            />
            </TableCell>

          <TableCell>

            <Button
            variant="contained"
            color="primary"
            onClick={lisaaSaatava}>
              Lisää saatava
            </Button>
          </TableCell>
        </TableRow>
        </TableBody>
      </Table>

      <Typography 
        variant="h4"
        style={{textAlign:"center", marginTop:20}}
      >Saatavasi ovat <b>{arraytest}</b> euroa.</Typography>

    </TableContainer>
        </div>
    )
}

export default Saatavat;
