import React, { useState } from 'react';
import { useEffect } from 'react';
import { HashRouter as Router, Route } from 'react-router-dom';
import Muokkaa from './component/Muokkaa';
import Navigaatio from './component/Navigaatio';
import Velkalista from './component/Velkalista';
import Saatavat from './component/Saatavat';
import MuokkaaSaatava from './component/MuokkaaSaatava';
import { Container, makeStyles, Typography } from '@material-ui/core';


const useStyles = makeStyles({
  laatikko: {
    marginTop: 10,
    width: 750,
    border: "solid black 2px",
    boxShadow: "0px 0px 3px black",
    padding: 20,
    backgroundColor:"lightgray",
    textAlign:"center"
  },
  teksti : {
      backgroundColor: "white",
      marginTop: 5,
      marginBottom : 10
  }
})

function App() {

  const tyylit = useStyles()
  const [velalliset, setVelalliset] = useState([]);
  const [saatavat, setSaatavat] = useState([]);

  const [velkaSumma, setVelkasumma] = useState();
  const [saatavaSumma, setSaatavasumma] = useState();



  useEffect(() => {
    if ( (localStorage.getItem("velalliset")) && (localStorage.getItem("velkaSumma")) ) {
      setVelalliset(JSON.parse(localStorage.getItem("velalliset")))
      setVelkasumma(JSON.parse(localStorage.getItem("velkaSumma")))
    }
    else {
      setVelalliset([])
      setVelkasumma(0)
    }
  }, [])

  useEffect(() => {
    if ( (localStorage.getItem("saatavat")) && (localStorage.getItem("saatavaSumma")) ) {
      setSaatavat(JSON.parse(localStorage.getItem("saatavat")))
      setSaatavasumma(JSON.parse(localStorage.getItem("saatavaSumma")))
    }
    else {
      setSaatavat([])
      setSaatavasumma(0)
    }
  }, [])

  useEffect(() => {
    localStorage.setItem("velalliset", JSON.stringify(velalliset))
  },[velalliset])

  useEffect(() => {
    localStorage.setItem("saatavat", JSON.stringify(saatavat))
  },[saatavat])

  useEffect(() => {
    localStorage.setItem("velkaSumma", JSON.stringify(velkaSumma))
  },[velkaSumma])

  useEffect(() => {
    localStorage.setItem("saatavaSumma", JSON.stringify(saatavaSumma))
  },[saatavaSumma])



  return (
    <Router>
      <Navigaatio/>

      <Route path="/" exact component={Velkalista}>
      <Velkalista
        setVelalliset={setVelalliset}
        velalliset={velalliset}
        setVelkasumma={setVelkasumma}
        velkaSumma={velkaSumma}
        />
      </Route>

      <Route path="/muokkaa/:id" exact component={Muokkaa}>
        <Muokkaa
        setVelalliset={setVelalliset}
        velalliset={velalliset}
        />
      </Route>

      <Route path="/muokkaasaatava/:id" component={MuokkaaSaatava}>
        <MuokkaaSaatava
        setSaatavat={setSaatavat}
        saatavat={saatavat}
        />
      </Route>

      <Route path="/saatavat" component={Saatavat}>
        <Saatavat
        setSaatavat={setSaatavat}
        saatavat={saatavat}
        setSaatavasumma={setSaatavasumma}
        saatavaSumma={saatavaSumma}
        />
      </Route>

      <Container className={tyylit.laatikko}>
      <Typography variant="h6">Velka: {velkaSumma} euroa</Typography>
      <Typography variant="h6">Saatavat: {saatavaSumma} euroa</Typography>
      {
      (saatavaSumma - velkaSumma > 0)
      ? <Typography variant="h4" style={{color:"green", textShadow:"1px 1px 2px black"}}><b>Arvioitu voitto: {saatavaSumma - velkaSumma} euroa</b></Typography>
      : (saatavaSumma - velkaSumma === 0) ? <Typography variant="h4" style={{textShadow:"1px 1px 2px black"}}><b>Tulos: {saatavaSumma - velkaSumma} euroa</b></Typography>
      : <Typography variant="h4" style={{color:"red", textShadow:"1px 1px 2px black"}}><b>Arvioitu tappio: {saatavaSumma - velkaSumma} euroa</b></Typography>
      }
    </Container>

    </Router>
    

  );
}

export default App;