import { AppBar, CssBaseline, Toolbar, Typography } from "@material-ui/core";
import { Link } from 'react-router-dom';

function Navigaatio() {

    return(
        <CssBaseline>
        <AppBar position="static" color="primary" style={{marginBottom: "20px"}}>
            <Toolbar>

                <Typography 
                variant="h2" 
                edge="start"
                style={{
                    marginTop:"10px",
                    marginBottom:"10px", 
                    marginRight:"0px", 
                    fontWeight:"bold", 
                    textShadow:"2px 2px 2px black"}}
                >Talouskirjanpito</Typography>

                <div style={{marginLeft:"200px"}}>
                    <Typography
                    component={Link}
                    to="/"
                    variant="h5" 
                    display="inline" 
                    style={{marginLeft:"20px", color:"white", textDecoration:"none", fontWeight:"bold", textShadow:"1px 1px 1px black"}}
                    >
                        Velkalista
                    </Typography>

                    <Typography
                    component={Link}
                    to="/saatavat"
                    variant="h5" 
                    display="inline" 
                    style={{marginLeft:"20px", color:"white", textDecoration:"none", fontWeight:"bold", textShadow:"1px 1px 1px black"}}
                    >
                        Saatavat
                    </Typography>
                </div>
            </Toolbar>
        </AppBar>
        </CssBaseline>
    );
}

export default Navigaatio